import React from 'react'
import Links from '../../components/Links'
import styles from './styles.scss'

class Layout extends React.Component {
    render() {
        return (
            <div className={styles.imageSize}>
                <Links />
                {this.props.children}
            </div>
        )
    }
}

export default Layout


